<template>
  <div class="chat-container">
    <Session
      @switchMessages="switchMessages"
      @switchShowDrawer="switchShowDrawer"
      @deleteMessage="deleteMessage"
      :showDrawer="showDrawer"
      :messagesList="messagesList"
    />
    <Bubble v-if="isShow" :prompts="prompts" @sendMsg="repeatText" />
    <div class="more-option" :class="!isMore && 'more-open'">
      <div class="mode">
        <el-switch
          v-model="isOnce"
          active-color="#13ce66"
          inactive-color="#2196f3"
        >
        </el-switch>
        <div
          class="mode-text iconfont"
          :class="
            isOnce ? 'icon-a-27Fdancixunhuanbofang' : 'icon-lianxuxianduan'
          "
        >
          模式： {{ isOnce ? "单次" : "连续" }}
        </div>
      </div>
      <div class="key-group">
        <el-button
          type="danger"
          @click="clearApiKey"
          icon="el-icon-delete"
        ></el-button>
        <el-input placeholder="本地保存你的key" v-model.trim="apiKey" clearable>
        </el-input>
        <el-button
          type="primary"
          @click="savaApiKey"
          icon="el-icon-key"
        ></el-button>
      </div>
      <div class="project-ads">
        <el-link type="primary" @click="navigatorUrl">github: 项目地址</el-link>
      </div>
      <div class="selector-container">
        <!-- 选择器代码 -->
        <el-select
          v-model="selectedPrompt"
          placeholder="预设话语"
          filterable
          @change="repeatText(selectedPrompt)"
          popper-class="prompt-selector"
        >
          <el-option
            v-for="prompt in prompts"
            :key="prompt.id"
            :label="prompt.text"
            :value="prompt.title"
          ></el-option>
        </el-select>
      </div>
    </div>
    <div class="chat-header">
      <div
        class="iconfont icon-navicon-dxmbwh session-open chat-header-item"
        @click="showDrawer = true"
      >
        会话记录
      </div>
      <div class="title" @click="isMore = !isMore">
        <div class="title-main">{{ title }}</div>
        <div
          class="more-btn iconfont icon-tabxiala-copy"
          :class="isMore && 'rotate'"
        ></div>
      </div>
      <div
        class="iconfont icon-faduanxin session-add chat-header-item"
        @click="addSession"
      >
        新的会话
      </div>
    </div>
    <div class="chat-messages">
      <div
        v-for="(message, index) in messages"
        :key="
          message.orgin_content && message.item
            ? message.orgin_content.slice(0, 10) + message.item.getDate()
            : index
        "
        :class="['chat-message', message.role]"
      >
        <div
          class="chat-content"
          :style="{ maxWidth: message.isExpan ? 'calc(100% - 20px)' : '70%' }"
        >
          <div class="chat-avatar">
            <img
              :src="require(`../assets/image/${message.role}.png`)"
              alt=""
              class="avatar"
            />
            <div
              class="iconfont icon-fuzhi content-btn copy-content"
              :data-clipboard-text="message.orgin_content"
            >
              <span class="opt-btn">复制</span>
            </div>
            <div
              v-if="message.role === 'user'"
              @click="sendMessage(message.orgin_content)"
              class="iconfont icon-zhongshi content-btn retry"
            >
              <span class="opt-btn">重试</span>
            </div>
            <div v-if="message.totalTime" class="total-time">
              {{ `本次响应共${message.totalTime}秒` }}
            </div>
            <div class="block-item"></div>
            <div
              v-if="!sending"
              class="expan iconfont"
              @click="switchExpan(index, !message.isExpan)"
              :class="
                message.role === 'user'
                  ? message.isExpan
                    ? 'icon-shaixuan-zhankai-right'
                    : 'icon-shaixuan-zhankai-left'
                  : message.isExpan
                  ? 'icon-shaixuan-zhankai-left'
                  : 'icon-shaixuan-zhankai-right'
              "
            ></div>
          </div>
          <div class="content" v-html="message.content"></div>
          <div class="iconfont icon-shijian-xianxing time">
            {{ (message.time || new Date()) | dateFormat }}
          </div>
        </div>
      </div>
    </div>

    <div class="chat-input">
      <div
        class="more-text iconfont icon-tabxiala-copy"
        :class="isMoreText && 'rotate'"
        @click="isMoreText = !isMoreText"
      ></div>
      <textarea
        v-model="inputText"
        :class="isMoreText && 'moreText'"
        placeholder="请输入消息"
      ></textarea>
      <button
        v-if="!sending"
        class="chat-send  iconfont icon-send"
        :class="{ 'chat-sending': sending }"
        @click="sendMessage"
      >
        SEND
      </button>
      <button
        v-else
        class="iconfont icon-tingzhi chat-send chat-stop"
        @click="stopSending"
      >
        STOP
      </button>
    </div>
  </div>
</template>

<script>
import { Select, Option, Input, Button } from "element-ui";
import { sendMsg, savaSession } from "../api/chat";
import marked from "marked";
import { retry } from "../utils/index";
import prompts from "../utils/prompts";
import hljs from "highlight.js/lib/core";
import javascript from "highlight.js/lib/languages/javascript";
import Session from "./Session.vue";
import Bubble from "./Bubble.vue";
import "highlight.js/styles/atom-one-dark.css";
hljs.registerLanguage("javascript", javascript);

export default {
  name: "Chat",
  data() {
    return {
      title: "聊天室",
      messages: [],
      messagesItem: undefined,
      inputText: "",
      sending: false,
      typingTimer: null,
      /** 会话列表 */
      showDrawer: false,
      messagesList: [],
      /** 是否是新会话 */
      isCreate: false,
      prompts: [],
      /** 新会话 */
      isShow: false,
      /** 单次会话 */
      isOnce: true,
      /** 头部的更多 */
      isMore: false,
      /** 选中的预设 */
      selectedPrompt: "",
      /** 展示更多文本 */
      isMoreText: false,
      /** apiKey */
      apiKey: ""
    };
  },
  components: {
    Session,
    Bubble,
    "el-select": Select,
    "el-option": Option,
    "el-input": Input,
    "el-button": Button
  },

  filters: {
    dateFormat(value) {
      const now = new Date();
      const time = new Date(value);
      const diff = (now - time) / 1000; // 毫秒转秒

      if (diff < 60) {
        return "刚刚";
      } else if (diff < 3600) {
        return `${Math.floor(diff / 60)}分钟前`;
      } else if (diff < 86400) {
        return `${Math.floor(diff / 3600)}小时${Math.floor(
          (diff % 3600) / 60
        )}分钟前`;
      } else if (diff < 2592000) {
        return `${Math.floor(diff / 86400)}天前`;
      } else if (diff < 31536000) {
        return `${Math.floor(diff / 2592000)}个月${Math.floor(
          (diff % 2592000) / 86400
        )}天前`;
      } else {
        const year = time.getFullYear();
        const month = time.getMonth() + 1;
        const day = time.getDate();
        const hour = time.getHours();
        const minute = time.getMinutes();
        const second = time.getSeconds();
        return `${year}年${month}月${day}日 - ${hour}时${minute}分${second}秒`;
      }
    }
  },
  created() {
    this.timer = setInterval(() => {
      this.$forceUpdate();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  mounted() {
    // 从本地存储中获取会话列表
    this.getMessagesList();

    // 注册代理事件
    this.handleRegProxyEvent();

    // 上传到session
    this.handleSavaToSession();

    // 弹层
    this.createPopup();

    this.prompts = prompts;
  },
  methods: {
    savaApiKey() {
      if (this.apiKey.length !== 51) return this.$message.warning("无效的key");
      localStorage.setItem("apiKey", JSON.stringify(this.apiKey));
      this.$message.success("保存成功！");
    },
    clearApiKey() {
      localStorage.setItem("apiKey", JSON.stringify(""));
      this.$message.success("清除成功！");
    },
    switchExpan(idx, flag) {
      this.$set(this.messages, idx, { ...this.messages[idx], isExpan: flag });
    },
    repeatText(flag) {
      this.inputText = flag;
    },
    switchIsShow(flag) {
      this.isShow = flag;
    },
    // 给复制按钮添加点击事件
    handleRegProxyEvent() {
      const parent = document.querySelector(".chat-messages"); // 父元素
      parent.addEventListener("click", event => {
        const target = event.target; // 点击的元素
        const cls = target.classList;
        if (cls.contains("copy-content")) {
          // 如果点击的是复制按钮
          const clipboard = new ClipboardJS(target);
          clipboard.on("success", () => {
            this.showSuccessMessage("复制成功！");
            // 复制成功后的操作
          });
          clipboard.on("error", () => {
            // 复制失败后的操作
            this.showErrorMessage("复制失败！");
          });
        }
        if (cls.contains("copy-button")) {
          const code = target.parentNode.querySelector("code");
          const codeContent = code.textContent;

          // 如果点击的是复制按钮
          const clipboard = new ClipboardJS(target, {
            text: () => codeContent
          });

          clipboard.on("success", () => {
            console.log("复制成功！");
            this.showSuccessMessage("复制成功！");
            // 复制成功后的操作
          });
          clipboard.on("error", () => {
            // 复制失败后的操作
            console.log("复制失败！");
            this.showErrorMessage("复制失败！");
          });
        }
      });
    },
    // 添加新的会话
    addSession() {
      this.isCreate = true;
      this.messages = [];
      this.switchIsShow(true);
      this.handleSavaToSession();
    },
    // 获取会话列表 和 key
    getMessagesList() {
      const messages = localStorage.getItem("messagesList");
      this.messagesList = messages ? JSON.parse(messages) : [];
      this.messagesItem = this.messagesItem || this.messagesList[0] || {};
      this.messages = this.messagesItem.list || [];
      this.apiKey = JSON.parse(localStorage.getItem("apiKey")) || undefined;
    },
    handleSavaToSession() {
      savaSession(
        this.messages
          .filter(
            item =>
              item.orgin_content &&
              !item.orgin_content.startsWith("回答发生了错误:") &&
              !item.orgin_content.startsWith("正在思索...")
          )
          .map(item => ({
            role: item.role,
            content: item.orgin_content
          })),
        this.apiKey
      );
    },
    // 保存会话列表
    saveMessagesList() {
      localStorage.setItem("messagesList", JSON.stringify(this.messagesList));
    },
    switchMessages(messages) {
      this.isCreate = false;
      this.isShow = false;
      this.messages = messages;
      this.messagesItem = messages || {};
      this.messages = this.messagesItem.list || [];
      this.handleSavaToSession();
    },
    switchShowDrawer(flag) {
      this.showDrawer = flag;
    },
    deleteMessage(idx) {
      const messages = localStorage.getItem("messagesList");
      this.messagesList = messages ? JSON.parse(messages) : [];
      this.messagesList.splice(idx, 1);
      this.saveMessagesList();
      /** 获取最新数据 */
      // this.getMessagesList()
    },
    sendMessage(msg = "") {
      this.switchIsShow(false);
      const text = typeof msg !== "object" ? msg : this.inputText.trim();
      if (!text) {
        return;
      }

      this.sending = true;
      this.typingTimer = setTimeout(() => {
        this.messages.push({
          role: "assistant",
          content: "正在思索...",
          time: new Date(),
          orgin_content: "正在思索...",
          isExpan: false
        });
      }, 500);
      // 重试3次请求
      retry(
        () => sendMsg(text, this.isCreate, this.isOnce, this.apiKey),
        3,
        500,
        (retries, error) => {
          this.addAssistantMessage(
            `回答发生了错误:${error.message} , 准备重试中, 剩余${retries ||
              0}次, 最长回答时间为2分钟, 如果回答时间比较长, 建议点击STOP换一个问题`
          );
        }
      )
        .then(response => {
          clearTimeout(this.typingTimer);
          this.messages.pop();
          this.messages.push({
            role: "user",
            content: this.renderMarkdown(text),
            time: new Date(),
            orgin_content: text,
            isExpan: false
          });
          const { content, totalTime } = response.data.data;
          this.addAssistantMessage(content, totalTime);
          this.inputText = "";
        })
        .catch(error => {
          clearTimeout(this.typingTimer);
          this.sending = false;
          this.messages.pop();
          this.addAssistantMessage(
            "回答发生了错误:" +
              error.message +
              "如果十分快的报错那么就是key出错，可能过期或者一个无效的key，请给一个正确的key，点击上方《聊天室》标题，输入保存你的key，PS:该项目没有使用数据库，并不会存储你的key，没有提供key，将使用我服务器默认的的key，如果我的账号额度用完，还想继续使用得用自己key，项目目前已经开源"
          );
        });
    },
    stopSending() {
      clearTimeout(this.typingTimer);
      this.sending = false;
      this.typing = false;
      this.messages.pop();
      // this.messages.pop();
    },
    addAssistantMessage(message = "", totalTime) {
      const typingMessage = {
        role: "assistant",
        content: "",
        time: new Date(),
        orgin_content: message,
        totalTime: totalTime ? totalTime : undefined,
        isExpan: false
      };
      this.messages.push(typingMessage);
      const markdownContent = this.renderMarkdown(message);
      const codeBlockRegex = /<pre><code.*?>[\s\S]*?<\/code><\/pre>/g;
      const codeBlocks = markdownContent.match(codeBlockRegex) || [];
      if (this.typing) {
        this.typing = false; // 停止打字机效果
        codeBlocks.forEach(codeBlock => {
          this.messages.push({ role: "assistant", content: codeBlock });
        });
      } else {
        const typingInterval = setInterval(() => {
          if (typingMessage.content.length < markdownContent.length) {
            typingMessage.content +=
              markdownContent[typingMessage.content.length];
          } else {
            clearInterval(typingInterval);
            this.sending = false;
            this.messages = this.messages.filter(
              item =>
                item.orgin_content &&
                !item.orgin_content.startsWith("正在思索...")
            );
            if (!message.startsWith("回答发生了错误:")) this.savaLocal();
          }
        }, 5);
      }
    },
    savaLocal() {
      /** 保存到本地存储 */
      if (this.isCreate || this.messagesList.length === 0) {
        if (!this.messages[0]) return;
        const _ = {
          title: this.removeTags(this.messages[0].content).slice(0, 10),
          list: this.messages
        };
        this.messagesList.push(_);
      } else {
        const idx = this.messagesList.findIndex(
          item => item.title === this.messagesItem.title
        );
        this.$set(this.messagesList, idx, {
          ...this.messagesList[idx],
          list: this.messages
        });
      }
      this.saveMessagesList();
      this.isCreate = false;
    },
    renderMarkdown(markdown) {
      return marked(markdown, {
        renderer: new marked.Renderer(),
        highlight: (code, language) => {
          // const validLang = hljs.getLanguage(language) ? language : "plaintext";
          const validLang = hljs.getLanguage(language)
            ? language
            : "javascript";
          const highlightedCode = hljs.highlight(validLang, code).value;
          const copyButton = `<div class="copy-button">复制代码</div>`;
          const codeBlock = `<pre><code class="hljs ${validLang}">${highlightedCode} </code>${copyButton}</pre>`;
          return codeBlock;
        }
      });
    },
    /** 创建弹层 */
    createPopup() {
      const _this = this;
      const chatMessagesDom = document.querySelector(".chat-messages");
      chatMessagesDom.addEventListener("mousedown", function(e) {
        const popup = document.querySelector(".popup-lang");
        if (popup) {
          document.body.removeChild(popup);
        }
      });
      chatMessagesDom.addEventListener("mouseup", function(e) {
        e.preventDefault();
        const selection = window.getSelection();
        const selectedText = selection.toString();
        if (!selectedText) return;
        const range = selection.getRangeAt(0);
        const rect = range.getBoundingClientRect();
        const selectionPosition = {
          top: rect.top + window.pageYOffset,
          left: rect.left + window.pageXOffset,
          width: rect.width,
          height: rect.height
        };

        const popup = document.createElement("div");
        popup.classList.add("iconfont", "icon-send", "popup-lang");
        const content = document.createElement("div");
        content.classList.add("popup-content");
        popup.innerText = "去发送";
        content.innerText = selectedText;
        popup.appendChild(content);
        popup.addEventListener("click", function(e) {
          !_this.sending && _this.sendMessage(selectedText);
        });
        document.body.appendChild(popup);

        const popupWidth = popup.offsetWidth;
        const popupHeight = popup.offsetHeight;
        const screenWidth = window.innerWidth;
        const screenHeight = window.innerHeight;

        let popupTop, popupLeft;
        if (selectionPosition.top - popupHeight > 0) {
          // 显示在选中文字上方
          popupTop = selectionPosition.top - popupHeight;
          popupLeft =
            selectionPosition.left +
            selectionPosition.width / 2 -
            popupWidth / 2;
        } else {
          // 显示在选中文字下方
          popupTop = selectionPosition.top + selectionPosition.height;
          popupLeft =
            selectionPosition.left +
            selectionPosition.width / 2 -
            popupWidth / 2;
        }

        // 检查弹层是否超出屏幕范围
        if (popupTop < 0) {
          popupTop = 0;
        }
        if (popupTop + popupHeight > screenHeight) {
          popupTop = screenHeight - popupHeight;
        }
        if (popupLeft < 0) {
          popupLeft = 0;
        }
        if (popupLeft + popupWidth > screenWidth) {
          popupLeft = screenWidth - popupWidth;
        }

        popup.style.top = popupTop + "px";
        popup.style.left = popupLeft + "px";
      });
    },
    removeTags(str) {
      return str.replace(/<\/?[^>]+>/gi, "");
    },
    showSuccessMessage(msg) {
      this.$message.success(msg);
    },
    showErrorMessage(msg) {
      this.$message.error(msg);
    },
    showWarningMessage(msg) {
      this.$message.warning(msg);
    },
    showInfoMessage(msg) {
      this.$message.info(msg);
    },
    navigatorUrl() {
      window.open("https://github.com/LianTianNo1/chat_serve");
    }
  }
};
</script>

<style scoped>
@media (max-width: 500px) {
  .opt-btn {
    display: none;
  }
  .content-btn {
    margin: 0 5px;
  }
}

.project-ads {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.key-group {
  display: flex;
  margin-bottom: 10px;
  width: 80%;
  display: flex;
  justify-content: center;
  position: relative;
  margin-top: 60px;
}
.key-group::before {
  width: 100%;
  display: block;
  content: "PS:该项目没有使用数据库，并不会存储你的key，没有提供key，将使用我服务器默认的的key，如果我的账号额度用完，还想继续使用得用自己key，项目目前已经开源";
  position: absolute;
  top: -60px;
  color: #fe5f55;
  font-size: 12px;
  left: 50%;
  transform: translateX(-50%);
}

.expan {
  transition: all 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  overflow: hidden;
  color: #eb87c3;
  cursor: pointer;
}
.block-item {
  flex: 1;
}
.more-option {
  position: absolute;
  display: flex;
  padding: 20px 0;
  border-radius: 0 0 20px 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.216);
  justify-content: center;
  top: 50px;
  width: 100%;
  flex-wrap: wrap;
  background: #ffffff8f;
  backdrop-filter: blur(20px);
  transition: all 0.5s;
  z-index: 1;
}

.more-open {
  top: 0;
  transform: translateY(calc(-100% - 10px));
}

.selector-container {
  /* 选择器样式 */
  position: relative;
  /* right: 10px; */
  /* top: 10px; */
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.title-main {
  line-height: 20px;
  height: 20px;
}
.more-btn {
  line-height: 20px;
  transition: transform 0.5s;
}
.rotate {
  transform: rotate(-180deg);
}
.selector-container /deep/ .el-input__inner {
  background-color: #ffffff61;
  backdrop-filter: blur(20px);
  color: #626262 !important;
}
.chat-container {
  display: flex;
  flex-direction: column;
  z-index: 0;
  height: 100vh;
  position: relative;
  background: url("../assets/image/bg.png");
  background-size: cover;
  z-index: 1;
}

.chat-container::before {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  content: "";
  display: block;
  background: rgba(255, 255, 255, 0.402);
  backdrop-filter: blur(30px);
}

.chat-header {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  background-color: #f5f5f5;
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
}

.option-group {
  display: flex;
}

.mode {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 10px;
}

.mode-text {
  font-size: 12px;
  height: 20px;
  line-height: 20px;
}

.session-add {
  margin-left: 12px;
}

.chat-header-item {
  cursor: pointer;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  padding-bottom: 80px;
}

.chat-message {
  display: flex;
  margin-bottom: 10px;
}

.chat-message.user {
  justify-content: flex-end;
}

.chat-content {
  max-width: 70%;
  padding: 10px;
  border-radius: 10px;
  /* background-color: #f5f5f5; */
  font-size: 16px;
  line-height: 1.5;
  word-wrap: break-word;
  background-color: rgba(255, 255, 255, 0.579);
  backdrop-filter: blur(20px);
  transition: all 0.5s;
}

.chat-content .content {
  border-bottom: 1px solid #a696c854;
  border-top: 1px solid #a696c854;
  margin-bottom: 5px;
  padding: 5px;
}

.chat-content /deep/ .copy-button {
  cursor: pointer;
  background: #282c34b5;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 0 0 10px 10px;
  color: #d0d0d0;
  font-size: 12px;
  transition: all 0.6s;
}

.chat-content /deep/ .copy-button:hover {
  background: #282c3469;
}

.chat-message.user .time {
  text-align: right;
}

.chat-avatar {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}

.chat-message.user .chat-avatar {
  flex-direction: row-reverse;
}

.chat-avatar .avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.chat-avatar .content-btn {
  padding: 0px 10px;
  border-radius: 10px;
  border: 1px solid white;
  background-color: #a696c8;
  backdrop-filter: blur(20px);
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 10px;
  height: 30px;
  overflow: hidden;
}
.chat-avatar .copy-content {
  background-color: #2470a0;
}
.chat-message.assistant .chat-content {
  background-color: rgba(255, 255, 255, 0.579);
  backdrop-filter: blur(20px);
}

.chat-typing {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.chat-typing-dot {
  width: 8px;
  height: 8px;
  margin-right: 4px;
  border-radius: 50%;
  background-color: #ccc;
  animation: chat-typing-dot 1s ease-in-out infinite;
}

@keyframes chat-typing-dot {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

.chat-input {
  z-index: 1;
  display: flex;
  align-items: center;
  height: 60px;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: calc(100vw - 20px);
  background-color: #f5f5f570;
  backdrop-filter: blur(20px);
  position: relative;
}

.more-text {
  position: absolute;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 100%;
  right: 20px;
  transition: transform 0.5s;
  border-radius: 20px;
  background-color: #f5f5f570;
  backdrop-filter: blur(20px);
  cursor: pointer;
}

.chat-input textarea {
  flex: 1;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #fff;
  font-size: 16px;
  line-height: 1.5;
  resize: none;
  transition: all 0.5s;
}
.moreText {
  height: 360px !important;
  margin-bottom: 320px;
}
.chat-input textarea:focus-visible {
  outline: none;
}
.chat-send {
  width: 80px;
  height: 40px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
  background-color: #a696c8;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.chat-send:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.chat-send.chat-sending {
  background-color: #ccc;
  pointer-events: none;
}

.chat-loading {
  display: flex;
  align-items: center;
}

.chat-loading-ring {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border: 2px solid #fff;
  border-top-color: transparent;
  border-radius: 50%;
  animation: chat-loading-ring 1s ease-in-out infinite;
}

@keyframes chat-loading-ring {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.chat-stop {
  background-color: #f5587b;
  color: #fff;
}

.chat-stop:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

/* 自定义样式 */
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #282c34;
  color: #abb2bf;
}

.hljs-comment,
.hljs-quote {
  color: #5c6370;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #c678dd;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e06c75;
}

.hljs-literal {
  color: #56b6c2;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #98c379;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #e6c07b;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #d19a66;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #61aeee;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}
</style>



