function retry(fn, retries = 3, interval = 1000, cb = () => {}) {
  return new Promise((resolve, reject) => {
    const attempt = () => {
      fn()
        .then(resolve)
        .catch(error => {
          if (retries === 0) {
            reject(error);
          } else {
            retries--;
            cb(retries, error);
            console.log("剩余重新求情次数" + retries);
            setTimeout(attempt, interval);
          }
        });
    };
    attempt();
  });
}

function shuffleArray(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
}

export { retry, shuffleArray };



