}
.popup-lang {
  position: absolute;
  border-radius: 5px;
  padding: 10px;
  background-color: #f5f5f570;
  backdrop-filter: blur(20px);
  z-index: 999;
  cursor: pointer;
  box-shadow: 1px 1px 8px #00000069;
  color: #f80080;
}
.popup-content {
  padding: 10px;
  border-top: 1px solid #ccc;
