<template>
  <div class="prompt-wrap">
    <transition-group name="prompt" tag="div" class="prompts-container">
      <div
        :style="{
          background: prompt.color,
          animationDelay: index * 0.1 + 's'
        }"
        class="prompt iconfont icon-zhongguohangtiantubiaoheji-weizhuanlunkuo-"
        v-for="(prompt, index) in prompts.slice(0, 30)"
        :key="prompt.id"
        @click="sendMsg(prompt.title)"
      >
        {{ prompt.text }}
      </div>
    </transition-group>
  </div>
</template>

<script>
import { shuffleArray } from "../utils/index";
export default {
  name: "Bubble",
  props: {
    prompts: {
      type: Array,
      required: true
    }
  },
  created() {
    this.prompts = shuffleArray(this.prompts);
  },
  methods: {
    sendMsg(msg) {
      this.$emit("sendMsg", msg);
    }
  }
};
</script>

<style scoped>
.prompt-enter-active,
.prompt-leave-active {
  transition: all 0.5s;
}

.prompt-enter,
.prompt-leave-to {
  opacity: 0;
  transform: translateY(20px);
}

.prompt-wrap {
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100% - 50px - 80px);
  top: 50px;
}

.prompts-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.prompt {
  cursor: pointer;
  border-radius: 10px;
  padding: 5px 10px;
  color: #fff;
  background: linear-gradient(135deg, rgb(220, 190, 182), transparent);
  animation: breath 2s ease-in-out infinite;
  opacity: 0;
  margin: 20px;
}

@media (max-width: 500px) {
  .prompt {
    margin: 0;
  }
}

@keyframes breath {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
</style>



