import request from "./request";

export function sendMsg(message, isCreate = false, isOnce = true, apiKey = "") {
  return request.post(`/generate`, {
    message,
    isCreate,
    isOnce,
    apiKey
  });
}

export function savaSession(messages, apiKey = "") {
  return request.post(`/savaSession`, {
    messages,
    apiKey
  });
}



