<template>
  <div
    class="drawer-demo"
    :style="{
      zIndex: showDrawer ? 2 : -1
    }"
  >
    <div
      class="drawer-mask"
      :style="{
        filter: `blur(${blur}px)`
      }"
      @click="switchShowDrawer(false)"
      v-show="showDrawer"
    ></div>
    <div class="drawer-content" :style="drawerStyle">
      <ul v-if="messagesList.length">
        <li
          v-for="(message, index) in messagesList"
          :key="message.title + index"
        >
          <div class="iconfont icon-huihua session-title">
            {{ message.title }}
          </div>
          <div class="btn_group">
            <div
              class="export iconfont icon-dakaixinxi"
              @click="switchMessages(message)"
            >
              打开
            </div>
            <div
              class="export iconfont icon-daochu"
              @click="exportMd(message.list)"
            >
              导出为md
            </div>
            <div
              class="delete iconfont icon-shanchu"
              @click="deleteMessage(index)"
            >
              删除
            </div>
          </div>
        </li>
      </ul>
      <div v-else class="empty">
        暂无会话
      </div>
    </div>
  </div>
</template>

<script>
import MarkdownIt from "markdown-it";
import { cloneDeep } from "lodash";
import { saveAs } from "file-saver";

export default {
  name: "DrawerDemo",
  data() {
    return {
      backgroundImage: "",
      blur: 5,
      drawerStyle: {
        width: "0%"
      }
    };
  },
  props: {
    showDrawer: Boolean,
    messagesList: {
      type: Array,
      default: []
    }
  },
  watch: {
    showDrawer() {
      this.$nextTick(() => {
        this.setDrawerWidth();
      });
    }
  },
  mounted() {
    this.setDrawerWidth();
  },
  methods: {
    setDrawerWidth() {
      const bodyWidth = document.body.offsetWidth;
      this.drawerStyle.width = this.showDrawer
        ? bodyWidth < 520
          ? "80%"
          : "40%"
        : "0%";
    },
    // 切换会话
    switchMessages(message) {
      console.log("message", message);
      this.$emit("switchMessages", message);
      this.switchShowDrawer(false);
    },
    switchShowDrawer(flag) {
      this.$emit("switchShowDrawer", flag);
    },
    deleteMessage(idx) {
      this.$emit("deleteMessage", idx);
    },
    convertToMarkdown(data) {
      let markdown = "";

      data.forEach((item, index) => {
        if (item.role === "user") {
          markdown += `\n## ${item.role} ${item.orgin_content.substr(
            0,
            20
          )}\n\n${item.orgin_content}\n\n`;
          markdown += `### 回答：\n\n`;
        } else if (item.role === "assistant") {
          markdown += `${item.orgin_content}\n\n`;
          if (index === data.length - 1 || data[index + 1].role === "user") {
            markdown += "\n";
          }
        }
      });

      return markdown;
    },
    exportMd(messages = []) {
      const md = new MarkdownIt();

      const content = this.convertToMarkdown(messages);

      const blob = new Blob([md.render(content)], { type: "text/markdown" });
      saveAs(blob, "conversation.md");
    }
  }
};
</script>

<style scoped>
.drawer-demo {
  position: absolute;
  height: 100%;
  width: 100%;
}

.drawer-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  background-color: rgba(0, 0, 0, 0.521);
}

.drawer-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  transition: width 0.5s ease;
  background-color: #ffffff4d;
  overflow: hidden;
  backdrop-filter: blur(9px);
}

.empty {
  width: 100%;
  height: 100%;
  font-size: 30px;
  font-weight: bold;
  color: #ffffff73;
  white-space: nowrap;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drawer-content ul {
  list-style: none;
  padding: 0;
  height: 100%;
  overflow: auto;
}

.drawer-content li {
  padding: 20px;
  background: #ffffff4f;
  backdrop-filter: blur(20px);
  color: black;
  cursor: pointer;
  transition: all 0.5s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-radius: 10px;
  flex-wrap: wrap;
}

.drawer-content li div {
  white-space: nowrap;
}

.drawer-content li:hover {
  background: #00000008;
  transform: translateY(-5px);
}

.drawer-content li:hover .session-title {
  color: rgb(255, 255, 255);
}
.btn_group {
  display: flex;
  flex-wrap: wrap;
}
.btn_group div {
  border-radius: 5px;
  padding: 5px;
  background-color: #fff;
  font-size: 12px;
  transition: all 0.2s;
  margin: 5px 0;
}

.btn_group div:hover {
  filter: brightness(0.9);
}
.session-title {
  margin: 5px 0;
  width: 120px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.btn_group .export {
  margin-right: 8px;
}

.btn_group .delete {
  background-color: #fd7013;
  color: #fff;
}
</style>



